import { useContext, useEffect, useRef, useState } from 'react';
import { useHistory } from 'react-router';
import { UserContext } from '../../Interface';
import { category } from '../../staticInfo';

const CreateListings = () => {
   const history = useHistory();
   const { state } = useContext(UserContext);
   const desc = useRef();

   useEffect(()=> {
      if(state.isSeller){
         history.push('/browse');
      }
   }, []);

   return (
      <div className="create__body">
         <div className="create__card">
            <div className="create__card-title">
               <h1>Tell us what you want to purchase.</h1>
               <h4>Let sellers from all over India fulfil your product needs.</h4>
            </div>
            <form className="create__form">
               <div className="create__form-name-category">
                  <label htmlFor="">Name of the product</label>
                  <input type="text" className="create__form-input"/>
                  <label htmlFor="">Category</label>
                  <select>
                     { category.map(item => (<option key={item.id} value={item.type}>{item.type}</option>)) }
                  </select>
               </div>
               <div className="create__form-desc">
                  <label htmlFor="">Tell us more about the product</label>
                  <textarea ref={desc} id="" cols="30" rows="10"></textarea>
               </div>
            </form>
         </div>
      </div>
   )
}

export default CreateListings
